import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

interface Points {
  active: boolean;
  points: number;
}

@Injectable({ providedIn: 'root' })
export class PointsService {
  private pointsSource = new BehaviorSubject<Points>({
    active: false,
    points: 0,
  });
  currentPoints = this.pointsSource.asObservable();

  constructor() {}

  changePoints(points: Points) {
    this.pointsSource.next({
      active: points.active,
      points: points.points,
    });
  }
}
